import React, { FC, createContext, ReactNode, ReactElement, useContext } from 'react';
import { useLocalStore } from 'mobx-react'
import RootStore from '@client/store/RootStore'

export const StoreContext = createContext<RootStore>({} as RootStore);

export type StoreComponent = FC<{
  store		: RootStore;
  children	: ReactNode;
}>;

export const StoreProvider: StoreComponent = ({
  	children
}): ReactElement => {
	const root_store = useLocalStore(()=>new RootStore())

	return (
		<StoreContext.Provider value={root_store}>
			{children}
		</StoreContext.Provider>
	)
}

export function useStore():RootStore {
	return useContext(StoreContext)
}